            <h3>Google Analytics 的「事件追蹤」埋設</h3>

            <h5>需求</h5>
            <ul class="block-wrap">
                <li>網站負責人想知道是「哪一篇文章」的「哪一個商品」的「哪一顆按鈕」被點擊。</li>
            </ul>

            <h5>解決方法</h5>
            <ul class="block-wrap">
                <li>透過 GA 事件追蹤，可以傳送按鈕的文字至 GA 後台做統計，但導購按鈕普遍都為相同的文字，若僅傳送此資料則無法得知被點擊的按鈕來自何處。為了讓網站負責人可更加詳細地知道使用者點擊的按鈕是哪一顆，我針對 All-in 各類型的文章結構，自行安排與規劃「事件動作」及「活動標籤」的呈現格式，抓取足以辨識按鈕位置的文字資料，並製作成逗號分隔的形式，以利負責人可匯入 excel 做統計。</li>
                <li>透過 jQuery 抓取 DOM 的按鈕，及足以辨識此按鈕位置的文字資料，將文字資料組合成逗號分隔的字串，利用 GA 提供的事件追蹤涵式，傳送至 GA 後台做統計。</li>
            </ul>

